html {
  position: relative;
  min-height: 100%;
}

footer{
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 38px;
  margin: 0 auto;
  color: #f4f4f4;
  border-top: 1px solid #222;
  background-color: #444;
  line-height: 38px;

  p{
    margin: 0;
    text-align: center;
  }
}